.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 4rem;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 20%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: 2rem;
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* section {
  justify-content: center;
  width: 40%;
  margin: 0;
} */

.menuSection {
  display: flex;
  /* flex-flow: wrap; */
  width: 100%;
}

div.order {
  float: right;
  width: 35%;
  max-height: 90%;
  background: white;
  margin: 0.5rem;
  margin-right: 1rem;
  border-radius: 1rem;
}
div.form-control {
  height: auto;
}
/*
form.card-body {
  float: right;
  position: absolute;
} */
div.container {
  display: flex;
  flex-flow: wrap;
  float: left;
  width: 70%;
  padding-right: 1rem;
  padding-left: 1rem;
  margin: 0.5rem;
}

dl.card {
  max-width: 12rem;
  height: auto;
  border-radius: 1rem;
  margin-right: 0.5rem;
  margin-left: 0.5rem;
}

dt.card-header {
  display: flex;
  height: auto;
  font-size: 1.5rem;
}
.card-body {
  height: auto;
  align-content: center;
}
dt.card-body {
  padding: .8rem;
}
p {
  margin-bottom: 0;
  font-size: 1.5rem;
}

button.btn.orderBtn {
  background: #fff;
  color: pink;
  border: solid pink;
  border-radius: 1rem;
}

button.closeBtn {
  position: right;
  background: none;
  border: none;
  font-size: 2rem;
  color: #aaa;
}
